<template>
	<div>
		<view>
			<!-- 未登录 -->
			<template v-if="!loginStatus">
				<view>未登录</view>
				
				<image :src="avatar" class="size-100 rounded-circle"></image>
			</template>
			
			<!-- 登录 -->
			<template v-else>
				
				<!-- 头像区域 -->
				<view class='flex align-center p-2'>
					<image :src="avatar" class="size-100 rounded-circle"></image>
					<view class="flex flex-column flex-1 px-3">
						<text class='font-md font-weight-bold text-muted' >{{nickname}}</text>
						<view class="">
							<text class='mr-1'>总贴数</text>
							<text class='text-warning mr-1'>{{posts}}</text>	
							<text class='mx-1'>今日发帖</text>
							<text class='text-warning'>{{dynamics}}</text>	
						</view>
					</view>
					<text class='iconfont icon-jinru' @tap="open('settings')"></text>
				</view>
				
				<!-- 数据区域 -->
				
				<view class="flex align-cneter p-2">
					<view class="flex flex-column flex-1 align-center justify-ceter">
						<text class='text-pick mb-1'  >{{posts}}</text>
						<text class=font-sm text-muted>帖子</text>
					</view>	
					<view class="flex flex-column flex-1 align-center justify-ceter">
						<text class='text-pick mb-1'  >{{posts}}</text>
						<text class=font-sm text-muted>动态</text>
					</view>	
					<view class="flex flex-column flex-1 align-center justify-ceter">
						<text class='text-pick mb-1'  >{{posts}}</text>
						<text class=font-sm text-muted>评论</text>
					</view>	
					<view class="flex flex-column flex-1 align-center justify-ceter">
						<text class='text-pick mb-1'  >{{posts}}</text>
						<text class=font-sm text-muted>粉丝</text>
					</view>	
					
				</view>
				
				<!-- banner区域 -->
				<view class="p-2">
					<image src="../../static/img/包.jpg" mode="aspectFill" class="w-100 rounded"></image>
				</view>
				
				
				<!-- 列表区域 -->
				
				<view class="m-2">
					<uni-list-item showExtraIcon title="浏览历史">
						<text slot="icon" class="iconfont icon-liulan text-body font-md"></text>
						
					</uni-list-item>
				</view>
				<view class="m-2">
					<uni-list-item showExtraIcon title="会员VIP">
						<text slot="icon" class="iconfont icon-huiyuanvip "></text>
					</uni-list-item>
				</view>
				<view class="m-2 "> 
					<uni-list-item showExtraIcon title="帖子审核">
						<text slot="icon" class="iconfont icon-keyboard "></text>
			
					</uni-list-item>
				</view>
				
				
			</template>
		</view>
	</div>
</template>

<script>
	import uniListItem from '@/components/uni/uni-list-item/uni-list-item.vue'
	export default {
		components:{
			uniListItem
		},
		data() {
			return {
				loginStatus:true,
				nickname:'曹大大',
				posts:20,
				dynamics:12,
				comments:18,
				
				// avatar:"",
			}
		},
		computed:{
			avatar(){
				return this.loginStatus===true ? '/static/img/me.jpg':'/static/img/nologin.jpeg'
			}
		},
		onNavigationBarButtonTap(){
			uni.showToast({
				title:'点击',
				duration:2000
			})
		},
		methods:{
			open(path){
				uni.navigateTo({
					url:`../${path}/${path}`,
				})
			}
		}
	}
</script>

